"use strict";
var router_1 = require("@angular/router");
var common_module_1 = require("./common_module");
var app_1 = require("./app");
var loadModule = function (moduleName) {
    return function () {
        return common_module_1.ModuleLoaderService.load(moduleName);
    };
};
// 定义模块 - URL 映射
var modules = [
    { path: 'demo1', module: 'demo1' }
];
var dynamicRoutes = [];
modules.forEach(function (m) {
    dynamicRoutes.push({
        path: m.path,
        loadChildren: loadModule(m.module),
    });
});
var appRoutes = [
    { path: '', component: app_1.HomeComponent }
].concat(dynamicRoutes);
console.log(appRoutes);
exports.routing = router_1.RouterModule.forRoot(appRoutes, { useHash: false });



